.container-main{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 100%;
    height: 80vh;
}

.heading-about{
    text-align: center;
}

.container-about{
    margin-top: 50px;
}

.heading{
    font-family: 'Poppins', sans-serif;
    font-size: 50px;
    font-weight: 600;
    color: #000;
    text-align: center;
    margin-bottom: 20px;
    width: 0ch;
    overflow: hidden;
    white-space: nowrap;
    animation: text 6s steps(18) infinite;
    border-right: 3px solid black;
}

@keyframes text{
    0%{
        width: 0ch;
    }
    50%{
        width: 16ch;
    }

}

.description{
    font-family: 'Poppins', sans-serif;
    font-size: 20px;
    font-weight: 300;
    color: #000;
    text-align: center;
    margin-bottom: 20px;
}

.button-main{
    color: #000;
    padding: 7px;
    border-radius: 20px;
    border: 1px solid #fff;
    box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.37);
    transition:  all 0.3s ease;
    text-decoration: none;
    margin: 20px;
    margin-top: 20px;
}